.find-dealer {
  background-size: cover;
  background-position: center;
  height: 100vh;
  display: flex;
  align-items: center;   // vertical center
  padding-left: 60px;    // push card from left edge
}

.dealer-card {
  background: #fff;
  padding: 40px;
  border-radius: 8px;
  width: 400px;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.2);

  h2 {
    font-size: 28px;
    font-weight: 700;
    margin-bottom: 16px;
  }

  p {
    color: #444;
    margin-bottom: 24px;
    font-size: 15px;
    line-height: 1.4;
  }

  .search-box {
    position: relative;
    margin-bottom: 20px;

    .search-icon {
      position: absolute;
      left: 14px;
      top: 50%;
      transform: translateY(-50%);
      color: #888;
      font-size: 16px;
    }

    input {
      width: 100%;
      padding: 14px 40px 14px 36px; // left padding for search icon
      border: 1px solid #ccc;
      border-radius: 30px;
      font-size: 14px;
    }

    .clear-btn {
      position: absolute;
      right: 14px;
      top: 50%;
      transform: translateY(-50%);
      background: none;
      border: none;
      font-size: 18px;
      cursor: pointer;
      color: #777;
    }
  }

  .divider {
    text-align: center;
    margin: 16px 0;
    color: #666;
    font-size: 14px;
  }

  .location-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 8px;
    width: 100%;
    padding: 14px;
    border: none;
    border-radius: 30px;
    background: #222;
    color: #fff;
    font-size: 15px;
    font-weight: 500;
    cursor: pointer;
    transition: background 0.3s ease;

    .icon {
      font-size: 16px;
      color: #1e90ff; // blue location icon
    }

    &:hover {
      background: #1e90ff; // sky blue hover
      color: #fff;
    }
  }
}
@media (max-width: 768px) {
  .hero-title {
    font-size: 2.5rem;
    top: 40%;
  }

  .hero-info {
    top: 55%;
    width: 90%;
  }

  nav a {
    font-size: 14px;
    margin: 0 10px;
  }

  button {
    padding: 8px 16px;
    font-size: 14px;
  }
}
