//3rd
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&display=swap');

.mission {
  position: relative;
  width: 100vw;
  height: 80vh;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Arial', sans-serif;
  overflow: hidden;
  color: white;
  /* Add a filter to brighten the background image */
  filter: brightness(1.3);  /* Increase brightness here */
}

.mission__overlay {
  position: absolute;
  inset: 0;
  background: rgba(0, 0, 0, 0.5); /* Slightly transparent black */
  z-index: 1;
  /* Optionally remove backdrop-filter if you don't want double brightening */
  /* backdrop-filter: brightness(1.2); */
}

.mission__content {
  position: relative;
  max-width: 500px;
  padding: 2rem 3rem;
  background: rgba(0, 0, 0, 0.7); /* Dark translucent background behind content */
  border-radius: 12px;
  text-align: center;
  z-index: 10;
}

.mission__title {
  font-size: 2.5rem;
  margin-bottom: 1rem;
  font-weight: 700;
  color: #fff;
}

.mission__description {
  font-size: 1.1rem;
  line-height: 1.5;
  margin-bottom: 1.5rem;
  color: #ddd;
}

.mission__button {
  background-color: #5aa9e6;
  color: white;
  border: none;
  border-radius: 30px;
  padding: 0.8rem 3rem;
  font-weight: 600;
  font-size: 1.1rem;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.mission__button:hover,
.mission__button:focus {
  background-color: white;
  color: #5aa9e6;
  outline: none;
}

@media (max-width: 768px) {
  .hero-title {
    font-size: 2.5rem;
    top: 40%;
  }

  .hero-info {
    top: 55%;
    width: 90%;
  }

  nav a {
    font-size: 14px;
    margin: 0 10px;
  }

  button {
    padding: 8px 16px;
    font-size: 14px;
  }
}
