body {
  margin: 0;
  padding: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #000;
  overflow-x: hidden;
}

* {
  box-sizing: border-box;
}

.App {
  max-width: 100vw;
  margin: 0 auto;
  overflow-x: hidden;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Poppins', sans-serif;
  background: #0a0a0a;
  color: #fff;
}

button {
  padding: 10px 20px;
  border-radius: 25px;
  border: none;
  cursor: pointer;
  transition: 0.3s ease-in-out;
}

button:hover {
  background: skyblue;
  color: #000;
}

/* ===========================
   Responsive Breakpoints
   =========================== */

/* Tablet */
@media (max-width: 1024px) {
  .hero {
    height: 500px;
  }

  .hero-title {
    font-size: 3rem;
    top: 18%;
  }

  .hero-info {
    bottom: 12%;
    font-size: 1rem;
  }

  .models-grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1rem;
  }

  .mission {
    padding: 2rem;
    text-align: center;
  }
}

/* Mobile */
@media (max-width: 768px) {
  .hero {
    height: 420px;
    text-align: center;
  }

  .hero-title {
    font-size: 2.2rem;
    top: 12%;
  }

  .hero-info {
    bottom: 8%;
    padding: 0 1rem;
  }

  .hero-buttons {
    flex-direction: column;
    gap: 0.75rem;
  }

  nav {
    flex-direction: column;
    align-items: center;
    gap: 0.5rem;
  }

  nav a {
    font-size: 14px;
  }

  .models-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }

  .mission {
    padding: 1.5rem 1rem;
  }

  .find-dealer {
    width: 90%;
    margin: auto;
    padding: 1rem;
  }

  footer {
    flex-direction: column;
    text-align: center;
    gap: 1rem;
  }
}

/* Small Mobile */
@media (max-width: 480px) {
  .hero {
    height: 350px;
  }

  .hero-title {
    font-size: 1.8rem;
    top: 10%;
  }

  .hero-info h2 {
    font-size: 1.2rem;
  }

  .hero-info p {
    font-size: 0.9rem;
  }

  button {
    padding: 8px 14px;
    font-size: 14px;
  }
}
